import React from "react";
import { Routes, Route, BrowserRouter, Navigate } from "react-router-dom";
import TacLogin from "../pages/public/tac-login/TacLogin";
import TacRegister from "../pages/public/tac-register/TacRegister";

const AuthRoute = () => {

    return (
        // <BrowserRouter>
        <Routes>
            <Route path={"/"} element={
                <TacLogin />
            } />
            <Route path={"/tac-register"} element={
                <TacRegister />
            } />

            <Route path={"*"} element={<Navigate to={"/"} replace />} />

        </Routes>
        // </BrowserRouter>
    )
}
export default AuthRoute;