import { TAC_LOGIN, TAC_REGISTER } from "../apiConfig/apiEndpoints";
import { apiCall } from "../apiConfig/apiCall";

export const tacLoginService = (data) => {
    return apiCall({
        url: TAC_LOGIN,
        method: "post",
        data,
    });
};

export const tacRegisterService = (data) => {
    return apiCall({
        url: TAC_REGISTER,
        method: "post",
        data,
    });
};